// export const domain = 'http://localhost:8080/fsi-credit-card-dispute-backoffice';
export const domain = 'http://' + window.location.host + '/entando-pam-app-5.0.3';

const CLIENT_ID = 'appbuilder';
const CLIENT_SECRET = 'appbuilder_secret';

export const login = () => {
  return new Promise((resolve, reject) => fetch(`${domain}/api/oauth/token`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        Authorization: `Basic ${btoa(`${CLIENT_ID}:${CLIENT_SECRET}`)}`,
      },
      body: `username=admin&password=adminadmin&grant_type=password&client_id=${CLIENT_ID}&client_secret=${CLIENT_SECRET}`,
    }).then(response => {
      if (response.ok) {
        response.json().then(json => {
          resolve(json.access_token);
        });
      }
    })
  );
}



