import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';

class Leaderboard extends Component {
  componentWillMount() {
     this.props.onWillMount();
   }
    render() {
        const { leaderboard } = this.props;
        const setClass = ['Leaderboard'];
        const options = {
            sizePerPage: 3,
            sizePerPageList: [ {
            text: '3', value: 3
            }, {
            text: '5', value: 5
            }, {
            text: '10', value: 10
            }, {
            text: 'All', value: leaderboard.length
            } ],
          };
    return (
        <div className={setClass.join(' ')}>
        <p className="Leaderboard__title"><strong>Leaderboard</strong> | Accomplishments</p>
           <BootstrapTable
            data={leaderboard }
            options={options}
            pagination
            striped
            >
           <TableHeaderColumn width={'3%'} dataField='rank' isKey></TableHeaderColumn>
           <TableHeaderColumn width={'90%'} dataField='name' columnTitle></TableHeaderColumn>
           <TableHeaderColumn width={'3%'} dataField='value' columnTitle></TableHeaderColumn>
          </BootstrapTable>
        </div>
      );
    }
}

Leaderboard.propTypes = {
  onWillMount: PropTypes.func,
};



export default Leaderboard;



