import { SET_DATA } from './types';
import { login, domain } from '../helpers/api';

export const setData = leaderboard =>  ({
  type: SET_DATA,
  payload: leaderboard,
});

// thunks

export const fetchData = () => dispatch => {
  login().then(token => {
    fetch(`${domain}/api/fsi/leaderboard`, {
      headers: {
        Authorization: `Bearer ${token}`
      }
    }).then(response => {
      if (response.ok) {
        response.json().then(json => {
          dispatch(setData(json));
        });
      }
      else {
        throw new Error('api request failed miserably ...');
      }
    });
  });
};



